<template>
  <div class="Items">
    <div class="holder">

      <form @submit.prevent="addSkill">
        <input type="text" placeholder="Enter an item you have.." v-model="item" v-validate="'min:5'" name="item">
        
        <transition name="alert-in" enter-active-class="animated flipInX" leave-active-class="animated flipOutX">
          <p class="alert" v-if="errors.has('item')">{{ errors.first('item') }}</p>
        </transition>
      </form>

      <ul>
        <transition-group name="item" enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
          <li v-for="(data, index) in items" :key='index'>
            {{ data.item }}
            <i class="fa fa-minus-circle" v-on:click="remove(index)"></i>
          </li>
        </transition-group>
      </ul>

      <p>These are your items.</p>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Items',
  data() {
    return{
      item: '',
      items: [
        { 'item': 'Babushka'},
        { 'item': 'Kompot'}
      ]
    }
  },
  methods: {
    addSkill() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          this.items.push({item: this.item})
          this.item = '';
        } else {
          console.log('not valid');
        }
      })
    },
    remove(id) {
      this.items.splice(id,1);
    }
  }
}
</script>

